.messenger{
    height: calc(100vh-70px);
    display:flex;
}
.chatMenu{
    flex:3.5;
}
.chatMenuInput{
    width:90%;
    padding: 10px 0;
    border:none;
    border-bottom: 1px solid gray;
}
.chatBox{
    flex:5.5;
}
.chatBoxTop{
    height:600px;
    overflow-y: scroll;
    padding-right: 10px;

}
.chatBoxBottom{
    margin-top:5px;
    display:flex;
    align-items:center;
    justify-content:space-between;
}
.chatMessageInput{
   width:80%;
   height:90px;
   padding:10px;

}
.chatSubmitButton{
   width:70px;
   height: 40px;
   border:none;
   cursor:pointer;
   border-radius:5px;
   background-color: teal;
   color:white
}
.chatOnline{
    flex:3;
}
.chatMenuWrapper,.chatBoxWrapper,.chatOnlineWrapper{
padding:10px;

}

.noConversation{
position:absolute;
top: 40%;
font-size: 50px;
color: rgb(224, 220, 220);
cursor:default
}