.item-detail-container {
  height: fit-content;
  background-color: #f2f4f5;
  display: flex;
  justify-content: center;
}
.item-content-section {
  /* border: 1px solid blue; */
  height: fit-content;
  display: flex;
  margin-top: 70px;
  width: 91%;
  justify-content: center;
}
.item-details {
  display: flex;
  justify-content: center;
  width: 60%;
  flex-direction: column;
}
.user-details {
  /* border: 2px solid blue; */
  width: 32%;
  display: flex;
  flex-direction: column;
  height: 89vh;
}
.product-content {
  display: flex;
  flex-direction: column;
  height: fit-content;
  justify-content: center;
  padding: 10px 0px 10px 6px;
  border: 1px solid rgba(0, 47, 52, 0.2);
  background-color: #fff;
}
.seller-contact {
  width: 96%;
  height: 33px;
  margin-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  cursor: pointer;
  background: black;
  color: #FFFF;
}
.seller-contact-number {
  width: 96%;
  height: 33px;
  margin-top: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  cursor: pointer;
}

.item-detail-description {
  height: 112vh;
  width: 91%;
}
.detail-attributes {
  width: 100%;
  height: 68%;
}
.description {
  height: 58%;
  background: #fff;
  text-align: left;
  width: 258px;
  display: flex;
  line-height: 26px;
  margin-left: 20px;
  font-size: 17px;
}
.details {
  background: #fff;
  height: fit-content;
  display: flex;
  flex-direction: column;
  padding: 17px 0px 22px 0px;
}
.details > span {
  padding-left: 20px;
  line-height: 46px;
  font-size: 26px;
  font-weight: 700;
  color: #bb0467;
}
.carousel-root {
  width: 91%;
}
.seller-detail {
  padding-left: 12px;
  height: 175px;
  margin-top: 3px;
  border: 1px solid rgba(0, 47, 52, 0.2);
  background-color: #fff;
}
.product-detail {
  height: 88vh;
  display: flex;
  flex-direction: column;
}
.profile-detail {
  display: flex;
  align-items: center;
  margin-top: 5px;
}
.carousel {
  background-color: #fff;
}
