.message{
    display: flex;
    flex-direction: column;
    margin-top:20px;
}
.messageTop{
    display:flex;
}
.messageImg{
    height:32px;
    width:32px;
    border-radius:50%;
    object-fit:cover;
}
.messageText{
padding:10px;
border-radius:20px;
background-color:#1877F2;
color:white;
max-width:300px;
}
.messageBottom{
    font-size:12px;
    margin-top:10px;
}
.message.own{
    align-items: flex-end;
}
.message.own .messageText{
 background-color: rgb(245,241,241);
 color:black;
}